.meals {
  /* max-width: 60rem; */
  width: -500px;
  margin: 1rem auto;
  -webkit-animation: meals-appear 1s ease-out forwards;
          animation: meals-appear 1s ease-out forwards;
 
}

.meals ul {
  list-style: none;
  margin: 0;
  padding: 0;
  margin: 40px;
  width:1400px;
 /* width: 1500px; */
  border: 2px solid rgb(80, 255, 80);

  display: grid;
  
  -webkit-align-content: center;
  
          align-content: center;
  grid-template-columns: 1fr 1fr ;
  grid-gap: 10px;
  background-color: white;
  padding: 40px;
  text-align: center;
  border: 2px solid black;
}

@-webkit-keyframes meals-appear {
  from {
    opacity: 0;
    -webkit-transform: translateY(3rem);
            transform: translateY(3rem);
  }

  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes meals-appear {
  from {
    opacity: 0;
    -webkit-transform: translateY(3rem);
            transform: translateY(3rem);
  }

  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
