.meals {
  /* max-width: 60rem; */
  width: -500px;
  margin: 1rem auto;
  animation: meals-appear 1s ease-out forwards;
 
}

.meals ul {
  list-style: none;
  margin: 0;
  padding: 0;
  margin: 40px;
  width:1400px;
 /* width: 1500px; */
  border: 2px solid rgb(80, 255, 80);

  display: grid;
  
  align-content: center;
  grid-template-columns: 1fr 1fr ;
  grid-gap: 10px;
  background-color: white;
  padding: 40px;
  text-align: center;
  border: 2px solid black;
}

@keyframes meals-appear {
  from {
    opacity: 0;
    transform: translateY(3rem);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}