.summary {
    text-align: center;
    max-width: 38rem;
    width: 100%;
    margin: auto;
    margin-top: -10rem;
    position: relative;
    background-color: #ff2828;
    color: white;
    border-radius: 14px;
    padding: 1rem;
    box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
  }
  
  .summary h2 {
    font-size: 1.2rem;
    margin-top: 0;
  }
  .summary h1 {
    font-size: 3rem;
    margin-top: 0;
  }